import {mapState} from 'vuex';
import chatPreview from '@/components/chat/preview/index.vue'
import {Swiper, SwiperSlide} from 'vue-awesome-swiper'
import 'swiper/swiper-bundle.css'
import _ from 'underscore'
import f from "@/application/functions";

export default {
  name: 'chatJoin',
  props: {
    chat: Object,
    usersinfo: Array
  },

  components: {
    chatPreview,
    Swiper,
    SwiperSlide
  },

  data: function () {

    return {
      loading: false,
      joinedMembers: []
    }

  },

  created: () => {

  },

  watch: {
    //$route: 'getdata'
  },
  computed: mapState({
    auth: state => state.auth,
    pocketnet: state => state.pocketnet,
    users: function () {
      let pStateUsers = this.$store.state.users
      let roomMembers = this.chat.currentState.members
      let pUsers = []
      let members = []
      _.mapObject(roomMembers, function (val, key) {
        console.log(roomMembers, "room members list //////")
        pUsers.push({id: f.getmatrixid(key)})
      })
      _.map(pStateUsers, function (id) {
        members.push(id)
      })

      this.joinedMembers = f.filterByUserId(pUsers, members)

      var users = _.filter(_.map(this.chat.currentState.members || [], (m) => {
        return this.$f.deep(this, '$store.state.users.' + m.name) || null
      }), (m) => {
        return m && m.id !== this.core.user.userinfo?.id
      })
      console.log(this.chat, "...../////list of joined members/////////")
      console.log('users', users)

      return users

    },
  }),


  methods: {
    join: function () {
      this.core.mtrx.client.joinRoom(this.chat.roomId).then(() => {
        this.$emit('joined')
      })
    },

    decline: function () {
      this.core.mtrx.client.leave(this.chat.roomId)
      this.$router.go(-1)
    }
  },
}