import {mapState} from 'vuex';

import list from '@/components/chat/list/index.vue'
import chatInput from '@/components/chat/input/index.vue'
import join from '@/components/chat/join/index.vue'
import metaUrlPreview from '@/components/events/previews/UrlPreviews/metaUrlPreview/index.vue'
import PNUrlPreview from '@/components/events/previews/UrlPreviews/PNUrlPreview/PNUrlPreview.vue'
import UrlPreview from '@/components/events/previews/UrlPreviews/UrlPreview.vue'

import Api from '../../application/api'
import f from '../../application/functions'
import _ from 'underscore'


export default {
  name: 'chat',
  props: {
    chat: Object,
    u: String,
  },

  components: {
    list,
    chatInput,
    join,
    metaUrlPreview,
    PNUrlPreview,
    UrlPreview
  },

  data: function () {
    return {
      loading: false,
      usersinfo: [],
      joinedRoomMembers: [],
      prevImage: undefined,
      selectedImg: String,
      metaDataPreview: {},
      metaPreview: false,
      metaPreviewUrl: String,
      filePreview: {
        type: Object,
        default: function () {
          return {
            show: false
          }
        },
      },
      anyFilePreview: {
        type: Object,
        default: function () {
          return {
            show: false
          }
        }
      },
      api: new Api(this, {pocketnet: 'https://pocketnet.app:8888'}),
      PNmetaPreview: false,
      UrlDataPreview: {},
      timeout: null
    }

  },

  created() {

  },

  mounted() {
    this.getuserinfo()
    this.$store.commit('active', true)
    this.$store.commit('blockactive', {value : true, item : 'chat'})

    // window.addEventListener('scroll', () => {
    //   // if(this.video) console.log('REFS', this.$refs.messageplyr.player)
    //   clg()
    // })
  },

  destroyed() {
    this.$store.commit('blockactive', {value : false, item : 'chat'})
    this.$store.commit('SET_CURRENT_ROOM', false)
  },

  watch: {
    chat: {
      immediate: true,
      handler: function () {
        if (this.chat)
          this.$store.commit('SET_CURRENT_ROOM', this.chat.roomId)
        else
          this.$store.commit('SET_CURRENT_ROOM', false)
      }
    }
    //$route: 'getdata'
  },

  computed: mapState({
    
    pocketnet: state => state.pocketnet,
    minimized : state => state.minimized,
    active : state => state.active,

    auth: state => state.auth,
    m_chat: function () {

      if (this.chat && this.chat.roomId) {
        var m_chat = this.core.mtrx.client.getRoom(this.chat.roomId)
        console.log("CHAT", m_chat)
        return m_chat || {}
      }
    },
    joinedMembers: function () {
      let self = this
      return this.core.mtrx.client.getJoinedRoomMembers(self.chat.roomId).then(r => {
        return _.mapObject(r.joined, function (key, value) {
          self.joinedRoomMembers.push(value)
        })
      })
    },
    downloadKeys: function (){
      let self = this
      let accessToken = this.core.mtrx.access.accessToken
       return self.core.mtrx.client.downloadKeysForUsers(this.joinedMembers, accessToken).then(r=>{
         return r
       })
    }

  }),

  methods: {
    hidePreview: function () {
      this.PNmetaPreview = false
    },
    newchat: function (chat) {
      this.$emit("newchat", chat)
    },

    sending: function () {
      this.$emit('sending')
    },

    getuserinfo: function () {
      if (this.u) {
        this.core.user.usersInfo(this.u).then(info => {
          this.usersinfo = info
        })
      }
    },
    membership: function () {
      // console.log('this.m_chat', this.m_chat)
      if (this.m_chat) {
        console.log("this.m_chat.getMyMembership()", this.m_chat.getMyMembership())
        return this.m_chat.getMyMembership()
      }

    },
    closing: function (e) {
      this.PNmetaPreview = e
    },
    usersinfoNames: function () {
      return _.map(this.usersinfo, function (u) {
        return u.name
      }).join(', ')
    },

    openImgM(img) {
      this.selectedImg = img
    },
    async getMetaUrl(url) {
      this.UrlDataPreview.is_pocketnet = false
      console.log('getMetaUrl invoked!')
      let ts = 0
      this.UrlDataPreview.metaPreviewUrl = url
      this.core.mtrx.client.getUrlPreview(url, ts).then(response => {
        this.UrlDataPreview.metaDataPreview = response
        this.PNmetaPreview = true
      }).catch(error => {
        console.log(error)
      })

      console.log('LEFT SIDE OBJ', this.UrlDataPreview)
    },
    getFilePreview(data) {
      this.filePreview = data
    },
    anyFileData(data) {
      console.log(this.anyFilePreview, "PREEEEEEEVIEW", data)
      this.anyFilePreview.name = data.file.name
      this.anyFilePreview.size = data.file.size
      console.log(this.anyFilePreview, "any previewsadasd")
    },

    getPNrequestData(url) {
      const parsed_url = f.poketnetUrlParser(url)

      if (parsed_url.post) {
        return {
          api_method: 'postInfo',
          parameters: parsed_url.post
        }
      } else return {
        api_method: 'pocketNetProfile',
        parameters: parsed_url.user
      }

      // if(parsed_url.user) {
      //   return {
      //     api_method: 'pocketNetProfile', 
      //     parameters: parsed_url.user 
      //   }
      // }

    },

    async createPocketNetProfile(url) {
      const meta_data = this.getPNrequestData(url)
      const address = await this.api.pocketnet.pocketNetProfileAddress({parameters: meta_data.parameters})
      console.log('address?', address)
      const response = await this.api.pocketnet.pocketNetProfileInfo({parameters: address[0].address})

      this.UrlDataPreview.is_pocketnet = true
      this.UrlDataPreview.header = response[0].name
      this.UrlDataPreview.text = decodeURIComponent(response[0].a)
      this.UrlDataPreview.userpic = response[0].i
      this.UrlDataPreview.followers = response[0].subscribers.length
      this.UrlDataPreview.following = response[0].subscribes.length
      this.UrlDataPreview.reputation = Math.trunc(response[0].reputation)
      this.UrlDataPreview.url = url
      this.PNmetaPreview = true
    },

    async createPocketNetPost(url) {

      this.UrlDataPreview.is_pocketnet = true
      this.UrlDataPreview.is_post = true
      const meta_data = this.getPNrequestData(url)
      console.log('PN DATA', meta_data)
      let response = await this.api.pocketnet.postInfo({parameters: meta_data.parameters})
      console.log('POST RESPONSE~~~~~', response)

      const url_text = decodeURIComponent(f.getTxt(response[0].m))

      this.UrlDataPreview.url = url
      this.UrlDataPreview.header = f.getUrl(url_text)
      this.UrlDataPreview.name = response[0].userprofile.name
      this.UrlDataPreview.text = url_text.length > 200 ? f.urlSeparator(f.textFormatter(url_text, 170)) : url_text
      this.UrlDataPreview.userpic = response[0].userprofile.i
      this.UrlDataPreview.tags = response[0].t.length > 5 ? response[0].t.slice(0, 5) : response[0].t
      this.UrlDataPreview.date = f.dateParser(response[0].time)
      this.UrlDataPreview.image = response[0].i[0]

      console.log('@@@!!!@@@', response[0].scoreCnt, response[0].scoreSum)

      this.UrlDataPreview.rating = {
        score: response[0].scoreCnt == 0 ? '0 / 0' : `${(response[0].scoreSum / response[0].scoreCnt).toFixed(1)} / ${response[0].scoreCnt}`,
        stars: f.setRating(parseInt(response[0].scoreSum), parseInt(response[0].scoreCnt))
      }

      let text_url = this.UrlDataPreview.header ? new URL(this.UrlDataPreview.header) : ''

      if (text_url) this.UrlDataPreview.video = f.videoUrlParser(text_url)
      this.PNmetaPreview = true
    },

    async getPNMetaPreview(url) {
      const meta_data = this.getPNrequestData(url)
      this.PNmetaPreview = false

      try {
        if (meta_data.api_method === 'postInfo') {
          this.createPocketNetPost(url)
          return
        }
        if (meta_data.api_method === 'pocketNetProfile') {
          this.createPocketNetProfile(url)
          return
        }
      } catch (error) {
        console.error(error)
      }
    },

    chatUrlHandler(url) {
      this.PNmetaPreview = false

      if (this.timeout) {
        clearTimeout(this.timeout)
      }

      this.timeout = setTimeout(() => {
        var newURL = new URL(url)
        newURL.hostname === 'pocketnet.app' ? this.getPNMetaPreview(url) : this.getMetaUrl(url)
      }, 500)

    },
    events(data) {
      this.$emit('getEvents', data)
      // console.log(data, "datin chat")
    }
  }
}