import f from "@/application/functions";
import {relativeTimeThreshold} from "moment";
import InputField from './InputField/InputField.vue'
import _ from "underscore";


export default {
  name: 'chatInput',
  props: {
    chat: Object,
    u: String,
  },


  components: {InputField},

  data: function () {

    return {
      upload: true,
      info: {},
      loading: false,
      text: '',
      file: {},
      fileInfo: {},
      menuItems: [
        {
          click: "cameraHandler",
          title: "Take photo or Video",
          icon: "fas fa-camera",

          upload: {
            multiple: true,
            extensions: ['jpg', 'jpeg', 'png'],
            images: {
              resize: {
                type: 'fit'
              }
            }
          }
        },

        {
          click: "fileHandler",
          title: "Send File",
          icon: "fas fa-sticky-note",

          upload: {
            multiple: true
          },
        }
      ],
      anyUrlMeta: String,
    }

  },


  watch: {
    //$route: 'getdata'
  },


  computed: {},

  created() {

  },

  mounted() {

  },

  methods: {
    emitInputData: function () {
      this.$emit('emptyInput')
      this.upload = true
    },
    HideUploadPic() {
      this.upload = false
    },
    emitUrl: function (url) {
      this.$emit('setMetaUrl', url)
    },
    newchat: function () {
      if (this.u) {

        this.$store.state.globalpreloader = true

        this.core.user.usersInfo(this.u).then(info => {

          var id = this.core.mtrx.kit.tetatetid(info[0], this.core.user.userinfo)

          var matrixId = this.core.user.matrixId(info[0].id)

          return this.core.mtrx.client.createRoom(
            {
              room_alias_name: id,
              visibility: 'private',
              invite: [matrixId],
              name: '#' + id,
              initial_state: [
                {
                  "type": "m.room.guest_access",
                  "state_key": "",
                  "content": {
                    "guest_access": "can_join"
                  }
                },
                {
                  "type": "m.room.encryption",
                  "state_key": "",
                  "content": {
                    "algorithm": "m.megolm.v1.aes-sha2"
                  }
                }
              ]
            }
          )

        }).then(chat => {

          this.$store.state.globalpreloader = false

          this.$store.commit('icon', {
            icon : 'success',
            message : "Chat started"
          })

          //this.$emit("newchat", chat)

        }).catch(e => {

          this.$store.state.globalpreloader = true

          ////catch error

          /*this.$store.commit('icon', {
            icon : 'error',
            message : "Can't start chat"
          })*/
        })

      }

    },

    maySendMessage() {
      return this.chat && this.chat.maySendMessage()
    },

    send: function (text, chatinput) {

      console.log('text', text);
      console.log('chatinput', chatinput);

      let whichMsg = f.getUrl(text.textContent)

      if (!whichMsg) {
        var t = text.textContent

        if (!this.chat) {
          this.newchat()
        }
        this.$emit("sending")

        this.$f.pretry(() => {

          return this.chat

        }).then(() => {
          return this.core.mtrx.client.sendEvent(this.chat.roomId, "m.room.message", {

            body: t,
            msgtype: "m.text"

          }, "").then(r => {
            console.log(r)
          }).catch(e => {
            console.log(e)
          })

        })
      } else {
        // let url = this.anyUrlMeta
        let url = whichMsg
        let ts = 0
        let txt = text.textContent
        this.anyUrlMeta = ''
        this.$emit('inputClean', false)
        this.$emit("sending")
        this.$f.pretry(() => {
          return this.chat

        }).then(() => {
          return this.core.mtrx.client.getUrlPreview(url, ts).then(response => {
            console.log('RESPONSE', response)
            return response
          })
        }).then((result) => {
          result.msg = f.getTxt(txt)
          result.url = f.getUrl(txt)
          chatinput.emojioneArea.setText('')
          return JSON.stringify(result)
        }).then(r => {
          console.log('R IS', r)
          return this.core.mtrx.client.sendMessage(this.chat.roomId, {
            body: r,
            msgtype: 'm.metaLink'
          })
        }).catch(e => {
          console.log(e)
        })
      }
    },

    sendImage: function (data) {
      console.log('data', data)
      this.$emit("sending")

      this.$f.pretry(() => {

        return this.chat

      }).then(() => {

        return this.core.mtrx.uploadContent(data.file)

      }).then((image) => {

        this.core.mtrx.client.sendImageMessage(this.chat.roomId, image, this.info, 'Image')
        this.$emit('previewImg', {})

      }).catch(e => {
        console.log(e)
      })
    },
    sendFile: function (data) {
      this.file = data.file
      console.log(data)
      this.$emit("sending")

      this.$f.pretry(() => {

        return this.chat

      }).then(() => {

        return this.core.mtrx.client.uploadContent(this.file)

      }).then(src => {
        return Promise.resolve(this.core.mtrx.client.mxcUrlToHttp(src))

      }).then((url) => {
        this.fileInfo.url = url
        this.fileInfo.name = this.file.name
        this.fileInfo.size = this.file.size
        let body = JSON.stringify(this.fileInfo)
        this.core.mtrx.client.sendMessage(this.chat.roomId, {
          body: body,
          msgtype: 'm.file'
        })
        this.$emit('anyFileSend', {})
        this.file = {}
      }).catch(e => {
        console.log(e)
      })
    },
    focus: function () {

    },

    blur: function () {
    },

    change: function () {
    },
    keyup: function (evt) {
      var value = evt.target.value

      if (value === '') {
        this.$emit('inputClean', false)
        return
      } else {
        this.$emit('inputClean', true)
      }

      this.text = value
      this.anyUrlMeta = f.getUrl(this.text)

      if (this.anyUrlMeta !== undefined) {
        this.$emit('setMetaUrl', this.anyUrlMeta)
      } else {
        this.$emit('inputClean', false)
      }

      if (this.chat)
        this.core.mtrx.client.sendTyping(this.chat.roomId, true, 100)
    },
    menuItemClickHandler(item, rowObject) {
      debugger
      this[item.click](rowObject);
      console.log(item, "item item")
    },
    menuItemLoadedHandler: function (value) {

      this.menuIsVisible = value

      return this.menuIsVisible
    },

    uploadStart(item, files) {
      console.log("uploadStartuploadStartuploadStartuploadStartuploadStartuploadStartuploadStart")
      
    },

    uploadError(item, error) {
      this.$store.commit('icon', {
        icon: 'error',
        message: error.text
      })
    },
    getImg() {
      return this.imgs = true
    },
    uploadSizeError(value) {
      if (!value) {
        this.$refs.dropdownMenu.hidePopup();
        console.log("error file size")
      }
    },
    uploadUploaded(item, data) {
      console.log("ASDASD")
      const validImageTypes = ['image/gif', 'image/jpeg', 'image/png'];

      if (!validImageTypes.includes(data.file.type)) {

        this.$emit('anyFileSend', data)
        this.sendFile(data)

      } else {

        this.$emit('previewImg', data)
        this.imageWH(data).then(info => {
          this.sendImage(data)
        })

      }
    },
    imageWH(file) {
      const img = new Image();
      var imgInfo = {};

      return new Promise((resolve, reject) => {

        img.onload = function () {
          imgInfo.w = this.width
          imgInfo.h = this.height
          resolve(imgInfo)
        }

        img.onerror = function () {
          reject()
        }

        img.src = file.base64
        this.info = imgInfo

      })


    },
    uploadUploadedAll(item, result) {
      this.$store.state.loading = false
      this.$refs.dropdownMenu.hidePopup();
    }

  },
}