<template>
  <div class="page chat">
    <topheader class="topheader" :u="u" :chat="chat" @closeAboutUser="closeAbout" @showAboutRoom="getClicked" :aboutUser="aboutShow" :roomInfo="roomInfo" @closeRoom="closingRoom"/>

    <maincontent v-if="!roomInfo">
      <template v-slot:content>
        <chat @sending="sending" @newchat="newchat" :u="u" :chat="chat" @getEvents="eventsRoom"/>
      </template>
    </maincontent>

<!--    <about :chat="m_chat" :events="events" @showAbout="aboutUser" @closeAboutRoom="roomInfoClose" v-else/>-->
    
  </div>
</template>

<style scoped lang="sass">

.topheader
  width: 100%
  top: 0
  z-index: 999

.aboutContact
  position: absolute
  left: 0
  right: 0
  top: 100px
  bottom: 0
  z-index: 999
  background: $color-applebuttontext
</style>

<script>

import chat from '@/components/chat/index.vue';
import topheader from '@/components/chat/topheader/index.vue';
import contact from '@/components/contact/index.vue';
import { mapState } from 'vuex';
import * as _ from "underscore";

export default {
  name: 'pagechat',
  components: {
    contact,
    chat,
    topheader,
  },
  data: () => {
    return {
      roomInfo: false,
      m_chat: {},
      events: [],
      aboutShow: false,
      aboutClose: false,
      aboutUserInfo: {},
    }
  },

  computed: {
    u() {
      return this.$route.query.u
    },
    chat() {
      var id = this.$route.query.id
      return this.$store.state.chatsMap[id]
    },

    ...mapState({
      pocketnet: state => state.pocketnet,
      minimized: state => state.minimized,
    }),

  },
  mounted() {
    
  },
  methods: {
    aboutUser(user){
      this.aboutUserInfo = user
      this.aboutShow = true
    },
    newchat(_chat) {

    },
    getClicked(room) {
      this.roomInfo = true
      this.m_chat = room
    },
    sending() {
      console.log("SENDING")
    },
    closingRoom(val) {
      this.roomInfo = val
    },
     eventsRoom(data){
      // console.log('event room', data)
      this.events = data
    },
    closeAbout(value) {
      this.aboutShow = value
    },
    roomInfoClose(value){
      this.roomInfo = value
    }

  },


}
</script>
